import React, { useEffect, useState } from "react"
import { Enrollment, User } from "../../../proto/ag_pb"
import { sortByField } from "../../Helpers"
import { useOvermind } from "../../overmind"
import { CourseGroup } from "../../overmind/state"




const CreateGroup = (props: {courseID: number}) => {
    const {state, actions} = useOvermind()
    const [cg, setCg] = useState<CourseGroup>({courseID: props.courseID, enrollments: [], groupName: "Group Name", users: []})
    const [query, setQuery] = useState<string>("")
    const self = state.enrollmentsByCourseId[props.courseID].getUser()

    useEffect(() => {
        if (cg.users.length == 0 && self) {
            actions.getEnrollmentsByCourse({courseID: props.courseID, statuses: [Enrollment.UserStatus.STUDENT]})
            cg.users.push(self)
            setCg(cg)
        }
    })


    const check = (query: string, user?: User): boolean => {
        if (user) {
            return !user.getName().toLowerCase().includes(query.toLowerCase())
        } 
        return true
    }

    const updateGroupUsers = (user?: User) => {
        if (user?.getId() === self?.getId()) {
            return
        }
        if (user) {
            if (!cg.users.includes(user)) {
                    cg.users.push(user)
            }
            else {
                cg.users = cg.users.filter(e => e.getId() != user.getId())
            }
            setCg({...cg})
        }
        console.log(cg)
    }

    const updateGroupName = (input: string) => {
        cg.groupName = input.length == 0 ? "Group Name" : input
        setCg({...cg})
    }

    return(
        <div className="container box"> 
            <div className="row">
                <div className="card well col-md-offset-2">
                    <div className="card-header" style={{textAlign: "center"}}>
                        Students
                    </div>
                    <input onKeyUp={e => setQuery(e.currentTarget.value)} placeholder={"Search"}></input>
                    <ul className="list-group list-group-flush">
                        {sortByField(state.courseEnrollments[props.courseID], [Enrollment.prototype.getUser], User.prototype.getName).map((user: Enrollment) => {
                            if (user.getUser()?.getName() !== state.self.name) {
                                return (
                                    <li hidden={check(query, user.getUser())} key={user.getUserid()} className="list-group-item">
                                        {user.getUser()?.getName()} 
                                        <i style={{float: "right"}} onClick={() => updateGroupUsers(user.getUser())}>+</i>
                                    </li>
                                )
                            }
                        })} 
                    </ul>
                </div>
                
                <div className='col'>
                    <div className="card well col-md-offset-2" >
                        <div className="card-header " style={{textAlign: "center"}}>{cg.groupName}</div>
                        <input placeholder={"Group Name:"} onKeyUp={e => updateGroupName(e.currentTarget.value)}></input>
                        {cg.users.map(user => {
                                return (
                                    <li key={user.getId()} className="list-group-item">
                                        <img id="group-image" src={user.getAvatarurl()}></img>
                                        {user.getName()} 
                                        <i style={{float: "right", cursor:"pointer"}} onClick={() => updateGroupUsers(user)}>-</i>
                                    </li>
                                    )
                            })} 
                        <div className="btn btn-primary" onClick={() => actions.createGroup(props.courseID)}> Create Group </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CreateGroup